import React, { useState, useEffect } from "react";
import header from "../assets/images/header.png"
import DropdownMenu from "./DropdownMenu";
import Particles from "react-tsparticles";
import ParticlesBackground from "react-tsparticles";

const ImageWithText = () => {
  const [text, setText] = useState(""); // tat pour le texte affich progressivement
  const [currentIndex, setCurrentIndex] = useState(0); // tat pour suivre l'index actuel des lettres
  const [isDeleting, setIsDeleting] = useState(false); // tat pour savoir si on est en train de supprimer les lettres
  const fullText = "N'hsitez pas  explorer mon portfolio !"; // Texte complet  afficher
  const typingSpeed = 90; // Vitesse d'apparition des lettres (en millisecondes)
  const pauseTime = 1000; // Temps de pause avant de redmarrer la boucle (en millisecondes)

  useEffect(() => {
    let timeoutId;

    if (!isDeleting && currentIndex < fullText.length) {
      // Cas o on ajoute des lettres
      timeoutId = setTimeout(() => {
        setText((prev) => prev + fullText[currentIndex]); // Ajoute la lettre suivante
        setCurrentIndex(currentIndex + 1); // Incrmente l'index
      }, typingSpeed);
    } else if (!isDeleting && currentIndex === fullText.length) {
      // Quand tout le texte est affich, mettre une pause avant de commencer  supprimer
      timeoutId = setTimeout(() => setIsDeleting(true), pauseTime); // Active la suppression aprs une pause
    } else if (isDeleting && currentIndex > 0) {
      // Cas o on supprime des lettres
      timeoutId = setTimeout(() => {
        setText((prev) => prev.slice(0, -1)); // Supprime la dernire lettre
        setCurrentIndex(currentIndex - 1); // Dcrmente l'index
      }, typingSpeed);
    } else if (isDeleting && currentIndex === 0) {
      // Quand tout le texte est supprim, mettre une pause avant de recommencer  ajouter les lettres
      timeoutId = setTimeout(() => setIsDeleting(false), pauseTime); // Dsactive la suppression pour recommencer
    }

    // Nettoyage du timeout  chaque itration pour viter les fuites
    return () => clearTimeout(timeoutId);
  }, [currentIndex, isDeleting, fullText, typingSpeed, pauseTime]);

  return (
    <div>
      {/* Image d'en-tte */}
      <div className="relative w-full h-screen">
        <img
          src={header}
          alt="Header"
          className="w-full h-full object-cover"
        />

        <div className="absolute top-0 left-0 w-full h-full"> 
          <ParticlesBackground />
        </div>

        {/* Bouton en haut  droite */}
        <DropdownMenu />

        {/* Conteneur du texte sur de l'image */}
        <div className="absolute inset-0 flex flex-col items-center justify-center">
          {/* Texte principal */}
          <h1 className="text-white text-5xl font-bold">Matteo PISELLI</h1>

          {/* Espacement entre les deux lments, si ncessaire */}
          <div className="h-10" />

          {/* Texte qui s'affiche lettre par lettre */}
          <h3 className="text-white text-3xl font-bold">
            {text} {/* Affichage progressif et rgressif du texte */}
          </h3>
        </div>
      </div>
    </div>
  );
};

export default ImageWithText;
